import React from "react";

interface Props {
  img: string;
  name: string;
  icon?: string;
  type?: string;
  technology?: string;
  description: string;
  url?: string;
}

export default function Project(props: Props) {
  return (
    <div
      className="m-3 p-3 border rounded-lg border-gray-100 cursor-pointer hover:border-gray-500"
      onClick={() => window.open(props.url)}
    >
      <img src={props.icon} alt="technology" className="h-8" />
      <div className="description">
        <p className="font-bold text-lg text-center p-3">
          {props.name.toUpperCase()}
        </p>
        <img src={props.img} alt="project screenshot" className="m-auto" />
        <p className="font-bold">{props.type}</p>
        <p className="p-3">{props.description}</p>
      </div>
    </div>
  );
}
