import React from "react";
import { Link } from "@reach/router";

interface Props {
  title: string;
  link: string;
  bgImage: string;
  className?: string;
}

export default function NavBlock(props: Props) {
  return (
    <div
      className={`w-full h-full rounded-full flex justify-center items-center bg-center bg-cover bg-blend-overlay text-white font-bold text-center ${props.className} filter hover:brightness-105`}
      style={{ backgroundImage: `url(${props.bgImage})` }}
    >
      <Link to={props.link} className="p-3">
        {props.title}
      </Link>
    </div>
  );
}
