import React from "react";
import { useMediaQuery } from "react-responsive";

interface Item {
  node: React.ReactNode;
  order?: number;
}
interface Props {
  items: Item[];
  startAngle?: number;
  radius: number;
  itemWidth?: number;
  itemHeight?: number;
  centerItem?: Item;
}
export default function Circle({
  items,
  radius,
  startAngle = 0,
  centerItem,
  itemHeight = radius / 2,
  itemWidth = radius / 2,
}: Props) {
  const angleStep = 360 / items.length;
  const isMobile = useMediaQuery({ maxWidth: 768 });

  if (items.length === 0) {
    return null;
  }

  if (isMobile) {
    return (
      <div className="flex flex-col justify-center items-center gap-3">
        {centerItem && (
          <div style={{ width: itemWidth, height: itemHeight }}>
            {centerItem.node}
          </div>
        )}
        <div className="flex flex-wrap items-center justify-center gap-3">
          {items.map((item, index) => (
            <div
              key={`circile-side-${index}`}
              style={{ width: itemWidth * 0.75, height: itemHeight * 0.75 }}
            >
              {item.node}
            </div>
          ))}
        </div>
      </div>
    );
  }

  return (
    <div
      className="relative rounded-full bg-gray-50"
      style={{
        width: radius * 2,
        height: radius * 2,
        margin: `${itemHeight / 2}px ${itemWidth / 2}px`,
      }}
    >
      {items.map((item, index) => {
        const angle = ((startAngle + angleStep * index) * 2 * Math.PI) / 360;
        const left = radius + Math.sin(angle) * radius - itemWidth / 2;
        const top = radius - Math.cos(angle) * radius - itemHeight / 2;
        return (
          <div
            className="absolute"
            key={`circle-side-${index}`}
            style={{
              top: top,
              left: left,
              zIndex: item.order || index,
              width: itemWidth,
              height: itemHeight,
            }}
          >
            {item.node}
          </div>
        );
      })}
      {centerItem && (
        <div
          className="absolute"
          style={{
            top: radius - itemHeight / 2,
            left: radius - itemWidth / 2,
            width: itemWidth,
            height: itemHeight,
          }}
        >
          {centerItem.node}
        </div>
      )}
    </div>
  );
}
